form{
    display: flex;
    flex-direction: column;
    padding: 4rem 1rem;
    margin: auto;
    max-width: 600px;
}

form input, form textarea{
    margin-bottom: 1rem;
    padding: 10px 18px;
    background-color: rgba(255, 255, 255, 0.1);
    border-color:rgba(255, 255, 255, 0.1);
    color: white;
    border-radius: 5px;
}